.hero {
  width: 100%;
  height: 95vh;
  padding: 0 30px 0 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 18px;
  background-image: url("/public/images/hero-image.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 60% 50%;
}
.hero .hero--subtitle,
.hero .hero--title {
  max-width: 550px;
  width: auto;
}
.hero .hero--subtitle {
  font-family: "Quicksand", sans-serif;
  font-size: 20px;
}
.hero .hero--title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 65px;
  line-height: 70px;
  color: rgb(63, 63, 63);
}
.hero .hero--aboutme-btn,
.hero .hero--book-appointment-btn {
  height: 50px;
  padding: 13px;
  background-color: #bf9773;
  border-radius: 6px;
  font-family: "Quicksand", sans-serif;
  font-weight: 600;
  font-size: 18px;
  color: white;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}
.hero .hero--aboutme-btn:hover,
.hero .hero--book-appointment-btn:hover {
  background-color: #dbb898;
}/*# sourceMappingURL=Hero.css.map */